import React from "react";
import Button from "../common/Button";
import Input from "../common/Input";


const ContactInfo = ({handleDeliveryClick, handleContactInfoClick}) => {

  return (
    <div className="flex flex-col">
      <h1 className="font-inter font-semibold text-35px mb-10">
         
      </h1>
      <div className="flex flex-row justify-between mb-10">
        <p className="font-inter font-semibold text-20px text-blue" onClick={handleContactInfoClick}>
           
        </p>
        <p className="text-gray font-inter font-semibold text-20px" onClick={handleDeliveryClick}></p>
      </div>
      <div className="flex flex-row gap-8">
        <Input
          classNameInput="text-custom-black border rounded-2xl max-w-md p-3"
          typeInput="text"
          placeholderInput=""
        />
        <Input
          classNameInput="text-custom-black border rounded-2xl max-w-md p-3"
          typeInput="text"
          placeholderInput=""
        />
      </div>
      <div className="flex flex-col gap-3 mt-10">
        <Input
          classNameInput="text-custom-black border rounded-2xl max-w-md p-3"
          typeInput="tel"
          placeholderInput=" "
        />
        <Input
          classNameInput="text-custom-black border rounded-2xl max-w-md p-3"
          typeInput="email"
          placeholderInput=" "
        />
      </div>
      <div className="flex flex-row gap-8 mt-10">
        <div className="flex flex-col gap-4">
          <Input
            classNameInput="text-custom-black border rounded-2xl max-w-md p-3"
            typeInput="text"
            placeholderInput=""
          />
          <Input
            classNameInput="text-custom-black border rounded-2xl max-w-md p-3"
            typeInput="text"
            placeholderInput=""
          />
        </div>
        <div className="flex flex-col gap-4">
          <Input
            classNameInput="text-custom-black border rounded-2xl max-w-md p-3"
            typeInput="text"
            placeholderInput=" "
          />
          <Input
            classNameInput="text-custom-black border rounded-2xl max-w-md p-3"
            typeInput="text"
            placeholderInput=" "
          />
        </div>
      </div>
      <Button
        classNameBtn="max-w-md bg-gray-dark mt-10 p-3 border rounded-2xl font-raleway font-700 text-18px text-white"
        nameBtn="submitForm"
        valueBtn="submit"
        onClickBtn={handleDeliveryClick}
      >
          
      </Button>
    </div>
  );
};

export default ContactInfo;



